@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap");


.queue {
    position: relative;
    height: 100vh;
    padding-top: 85px;
}

.queue__list {
    list-style: none;

    max-height: 980px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}
.queue__lists {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    width: 60%;
}
.queue__list li {
    display: flex;
    font-size: 90px;
    color: #d2d2d2;
}
li.queue__list-text {
    color: #f6534a;
    font-size: 40px;
    margin-bottom: 40px !important;
}
.queue__list-text span:first-child {
    margin-right: 85px;
}
.queue__list li.active {
    color: #f6534a;
}
.queue__list li:nth-child(-n + 6) {
    min-width: 470px;
    margin-right: 215px;
}
.queue__list li:not(:last-child) {
    margin-bottom: 25px;
}
.queue__list-client {
    position: relative;
}
.queue__list-client::after {
    content: ">";
    display: inline-block;
    margin: 0 40px;
}
.queue__inner {
    display: flex;
    justify-content: space-between;
    max-width: calc(100vw - 160px);
    width: 100%;
    min-width: 280px;
    margin: 0 auto;
    padding: 0 15px;
    height: calc(100% - 75px);
}
.queue__date .time {
    font-size: 200px;
    color: #f6534a;
}
.queue__date .date {
    font-size: 72px;
    color: #f6534a;
    text-align: right;
}
.queue__date .date span {
    display: block;
}

.queue__date {
    width: 30%;
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}
@media (max-width: 1880px) {
    .queue__list li {
        font-size: 65px;
    }
    .queue__list {
        max-height: 760px;
    }

    .queue__list li:nth-child(-n + 6) {
        min-width: 345px;
        margin-right: 90px;
    }
    li.queue__list-text {
        font-size: 35px;
    }
    .queue__date .time {
        font-size: 155px;
    }
    .queue__date .date {
        font-size: 50px;
    }
}
@media (max-width: 1440px) {
    .queue__list li:not(:last-child) {
        margin-bottom: 20px;
    }
    .queue__list-client::after {
        margin: 0 25px;
    }
    .queue__list li {
        font-size: 50px;
    }
    li.queue__list-text {
        font-size: 30px;
        margin-bottom: 30px !important;
    }
    li.queue__list-text span:first-child {
        margin-right: 40px;
    }
    .queue__date .time {
        font-size: 130px;
    }
    .queue__date .date {
        font-size: 35px;
    }
    .queue__list {
        max-height: 600px;
    }
    .queue__list li:nth-child(-n + 6) {
        min-width: 240px;
    }
}
@media (max-width: 1115px) {
    .queue__list li {
        font-size: 36px;
    }
    li.queue__list-text {
        font-size: 21px;
        margin-bottom: 25px !important;
    }
    .queue__list li:nth-child(-n + 6) {
        margin-right: 50px;
        min-width: 190px;
    }
    .queue__date .time {
        font-size: 90px;
    }
    .queue__date .date {
        font-size: 25px;
    }
    .queue__list {
        max-height: 460px;
    }
}
