.employees_schedule_calendar {
  display: flex;
  flex-flow: column nowrap;
}

.employees_schedule_week {
  display: flex;
  flex-flow: row nowrap;
  border-top: 1px solid #dcdfe2;
  border-left: 1px solid #dcdfe2;
  border-right: 1px solid #dcdfe2;
}

.employees_schedule_week:last-child {
  border-bottom: 1px solid #dcdfe2;
}

.employees_schedule_day {
  position: relative;
  display: flex;
  flex-flow: column;
  min-height: 130px;
  width: 100%;
  border-right: 1px solid #dcdfe2;
  background-color: #fff;
}
.employees_schedule_day:last-child {
  border-right: none;
}

.employees_schedule_date {
  position: absolute;
  right: 4px;
  bottom: 0;
  font-weight: 500;
}

.employees_schedule_weekdays {
  display: flex;
  flex-flow: row nowrap;
}

.employees_schedule_weekday {
  display: flex;
  width: 100%;
  justify-content: center;
}

.employees_schedule_DateInterval {
  padding: 4px 0;
  background: #8ace98;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  height: 30px;
  cursor: pointer;
}

.employees_schedule_DateIntervalException {
  padding: 4px 0;
  background: #74a6d4;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  height: 30px;
  cursor: pointer;
}

.employees_schedule_attachedCabinets {
  padding: 4px 0;
  background: #f86964;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  height: 30px;
  cursor: pointer;
  text-align: center;
}

.employees_schedule_day_add {
  display: flex;
  height: 30px;
  background-color: #d9dfdb;
  width: 100%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 20px;
}

.employees_schedule_day_add:hover {
  font-weight: 500;
}
.employees_schedule_day_add:active{
  font-weight: 200;
}

.employees_schedule_day_popup {
  padding: 22px;
  background-color: white;
  border-radius: 0.42rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0,0,0, 0.1);
  outline: 0;
}

.employees_schedule_day.weekend {
  background-color: #c29d9b;
  cursor: pointer;
}

.employees_schedule_day_add-form {
  width: 450px;
}

.employees_schedule_month-picker {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-left: 25px;
}

.employees_schedule_month-picker_button {
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: bold;
}

.employees_schedule_month-picker_date {
  display: flex;
  justify-content: center;
  width: 100px;
}

@media(max-width: 767px){
  .employees_schedule_calendar{
    min-width: 750px;
  }
  .form-label-right{
    overflow-x: scroll;
  }
}

.employees_schedule_add_rule {
  display: flex;
  margin-bottom: 25px;
}

