/**
 * Перекраска меню
 */

.brand-dark .brand,
.aside-menu .menu-nav > .menu-item.menu-item-active > .menu-link,
.menu-nav > .menu-item:not(.menu-item-parent):not(.menu-item-open):not(.menu-item-here):not(.menu-item-active):hover > .menu-link {
  background-color: #FF564F !important;
}

#kt_aside,
#kt_aside_menu {
  background-color: #F14942 !important;
}

.menu-nav > .menu-item.menu-item-active > .menu-link .menu-text,
.aside-menu .menu-nav > .menu-item > .menu-link .menu-text {
  color: #fff !important;
}

.aside-menu .menu-nav > .menu-section .menu-text {
  font-weight: 600 !important;
  color: #fff !important;
}

.aside-menu .menu-nav > .menu-item:not(.menu-item-parent):not(.menu-item-open):not(.menu-item-here):not(.menu-item-active):hover > .menu-heading .menu-icon.svg-icon svg g [fill],
.aside-menu .menu-nav > .menu-item:not(.menu-item-parent):not(.menu-item-open):not(.menu-item-here):not(.menu-item-active):hover > .menu-link .menu-icon.svg-icon svg g [fill],
.aside-menu .menu-nav > .menu-item.menu-item-active > .menu-heading .menu-icon.svg-icon svg g [fill],
.aside-menu .menu-nav > .menu-item.menu-item-active > .menu-link .menu-icon.svg-icon svg g [fill],
.brand-dark .brand .btn .svg-icon svg g [fill] {
  fill: #fff !important;
}

.aside-menu .menu-nav > .menu-item > .menu-heading .menu-icon.svg-icon svg g [fill],
.aside-menu .menu-nav > .menu-item > .menu-link .menu-icon.svg-icon svg g [fill] {
  fill: #FF6D67 !important;
}

.aside .aside-menu.ps > .ps__rail-y > .ps__thumb-y, .aside .aside-menu.ps > .ps__rail-y > .ps__thumb-y:focus,
.aside .aside-menu.ps > .ps__rail-y > .ps__thumb-y:hover {
  background: #fff !important;
}

#kt_header_mobile img[ alt="logo" ] {
  max-height: 35px !important;
}

.react-bootstrap-table-cell {
  cursor: pointer;
}

.react-bootstrap-table-cell:first-letter {
  text-transform: uppercase;
}

.react-bootstrap-table-cell__filtrable {
  border-bottom: 1px dashed #000080;
}

#kt_header > div {
  padding: 0 25px;
}

.modal-backdrop.show {
  opacity: 0.5;
}
