.schedule_cell_add {
  cursor: pointer;
}
.schedule_cell_add_void {
  cursor: default;
}

.schedule_cell_add_content {
  width: 20px;
  height: 20px;
  position: relative;
  border-radius: 50%;
  border: 1px solid #b8b3b3;
}

.schedule_cell_add:hover {
  background-color: #ffe9e8;
  border-color: #ffa6a1;
}

.schedule_cell_add_content::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: #b8b3b3;
  width: 7px;
  height: 1px;
}

.schedule_cell_add_content::after {
  transform: translateY(-50%) rotate(-90deg);
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: #b8b3b3;
  width: 7px;
  height: 1px;
}
